import React from "react";
import "./header.css";
import whiteHamburgerIcon from "../images/white-hamburger-icon.svg";

class Header extends React.Component {
	constructor(props) {
		super(props);
		this.state = { headerClass: "", dropDownClass: "" };
	}

	componentDidMount = () => {
		window.addEventListener("scroll", this.isScrolled);
		window.onresize = () => this.isScrolled();
	};

	isScrolled = () => {
		const body = document.querySelector("body");
		let windowSize = window.innerWidth;
		let scrollPosition = body.getBoundingClientRect().top;

		if (this.state.dropDownClass === "drop-menu") {
			this.setState({ dropDownClass: "raise-menu" });
		}

		if (windowSize <= 700) {
			if (scrollPosition !== 0) {
				this.setState({ headerClass: "header-thin1" });
			} else {
				this.setState({ headerClass: "header-full2" });
			}
		} else if (windowSize <= 950) {
			if (scrollPosition !== 0) {
				this.setState({ headerClass: "header-thin" });
			} else {
				this.setState({ headerClass: "header-full1" });
			}
		} else {
			if (scrollPosition !== 0) {
				this.setState({ headerClass: "header-thin" });
			} else {
				this.setState({ headerClass: "header-full" });
			}
		}
	};

	slideMenu = () => {
		return this.state.dropDownClass === "drop-menu"
			? this.setState({ dropDownClass: "raise-menu" })
			: this.setState({ dropDownClass: "drop-menu" });
	};

	componentWillUnmount() {
		window.removeEventListener("scroll", this.handleScroll);
	}

	render() {
		return (
			<header>
				<div className={`header-title ${this.state.headerClass}`}></div>
				<div className='dropdown'>
					<img
						src={whiteHamburgerIcon}
						alt='white menu icon'
						className='hamburger-menu'
						onClick={this.slideMenu}
					/>
					<div className={`dropdown-content ${this.state.dropDownClass}`}>
						<div className='dropdown'>
							<div className='header-group drop'>
								<a className='header-tab' href='#home'>
									Home
								</a>
								<div className='underline drop'></div>
							</div>

							<div className='header-group drop'>
								<a className='header-tab' href='#contact'>
									Contact
								</a>
								<div className='underline drop'></div>
							</div>
						</div>
					</div>
				</div>
				<nav className='tabs-section'>
					<div className='header-group'>
						<a className='header-tab' href='#home'>
							Home
						</a>
						<div className='underline'></div>
					</div>
					<h3 className='divider'>|</h3>

					<div className='header-group'>
						<a className='header-tab' href='#contact'>
							Contact
						</a>
						<div className='underline'></div>
					</div>
				</nav>
			</header>
		);
	}
}

export default Header;
